.modal-content {
    background-color: white;
    outline: none;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: auto;
}

.loaded-image {
    width: 100%;
    height: auto;
    max-height: 350px;
}

@media only screen and (min-width: 400px) and (max-width: 700px) {
    .modal-content-size {
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
        width: 80%;
    }
}

@media only screen and (min-width: 700px) {
    .modal-content-size {
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
        width: 50%;
    }
}


